$blue-color: #00acdc;
$grey-color: #616070;
$black-color: #060b35;
$card-size: 200px;
$cards-in-row: 4;
$gaps: 48px;
$gaps-qty: calc(#{$cards-in-row} - 1);
$cards-width: calc(#{$card-size} * #{$cards-in-row});
$gaps-width: calc(#{$gaps} * #{$gaps-qty});
$catalog-width: calc(#{$cards-width} + #{$gaps-width});
